import React from 'react';
import { branch } from 'baobab-react/higher-order';

import actions from '../../utils/mixin.actions.branch';

const HubConfig = props => {
  const { uuid, hubs } = props;
  const { name } = hubs[uuid];

  return (
    <div className="modal-content hub">
      <div className="modal-header">
        <h3 className="row">{ name }</h3>
        <h4 className="row">{ uuid }</h4>
      </div>
      <div className="modal-body" />
      <div className="modal-footer" />
    </div>
  );
};

export default branch({
  hubs: ['hubs'],
}, actions(['hubs', 'navigation'], HubConfig));
