import { transform, each } from 'lodash';

import state from './state';
import clients from './clients';
import logger from '../utils/logger';

import navigation from './actions/navigation';
import hubs from './actions/hubs';

const helpers = {
  getCallback(a, b) {
    if (typeof a === 'function') return a;
    if (typeof b === 'function') return b;
    return () => {};
  },
  getParams(a) {
    return typeof a === 'object' ? a : {};
  },
};

export default transform(
  {
    navigation,
    hubs,
  },
  (actions, collection, group) => {
    actions[group] = actions[group] || {};
    each(
      collection,
      (action, name) => {
        actions[group][name] = (_params, _callback) => {
          const params = helpers.getParams(_params);
          const callback = helpers.getCallback(_params, _callback);

          logger.info(`ACTION[${group}.${name}]`);
          logger.debug(
            `ACTION[${group}.${name}]`,
            { params, callback }
          );
          action(
            {
              callback,
              clients,
              state,
              actions,
              logger,
            },
            params
          );
        };
      }
    );
  },
  {}
);
