import React from 'react';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faBatteryFull,
  faBatteryThreeQuarters,
  faBatteryHalf,
  faBatteryQuarter,
  faBatteryEmpty,
} from '@fortawesome/free-solid-svg-icons';

const icons = [
  faBatteryEmpty,
  faBatteryQuarter,
  faBatteryHalf,
  faBatteryThreeQuarters,
  faBatteryFull,
];

export default props => {
  const { level = 0 } = props;
  return (
    <div className="hubs battery" title={ `Battery level: ${level}%` }>
      <FontAwesomeIcon icon={ icons[Math.round(level / 25)] } />
    </div>
  );
};
